import { useEffect, useState } from "react";
import Item from "./Item";
import CallAPI from "../util/CallAPI";
import * as Constant from "../Constant/Constant";
import { CHARACTER_URL } from "../Constant/Constant";
import { LazyLoadImage } from "react-lazy-load-image-component";

function Container() {
  const [characters, setCharacters] = useState(null);
  useEffect(() => {
    CallAPI(Constant.CHARACTER_URL, "", "GET", null).then((res) => {
      setCharacters(res.data);
    });
  }, []);

  const generateItem = () => {
    if (characters) {
      var groupSize = 5;
      var rows = characters
        .map((character, index) => {
          return (
            <Item key={index} name={character}>
              <LazyLoadImage
                alt={`${character}-card`}
                src={`${CHARACTER_URL}/${character}/card`}
                effect="opacity"
              />
            </Item>
          );
        })
        .reduce(function (r, element, index) {
          index % groupSize === 0 && r.push([]);
          r[r.length - 1].push(element);
          return r;
        }, [])
        .map((rowContent, index) => {
          return (
            <div key={index} className="row">
              {rowContent}
            </div>
          );
        });
    }
    return rows;
  };

  generateItem();

  return (
    <div className="row" style={{ width: "90%", margin: "0 auto" }}>
      {characters && generateItem()}
    </div>
  );
}

export default Container;
