import { useRouteMatch } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronRight } from "@fortawesome/free-solid-svg-icons";
import { NavLink } from "react-router-dom";
import { GIT_PROJECT } from "../Constant/Constant";

const CharacterInfo = () => {
  const match = useRouteMatch();
  const name = match.params.name;
  return (
    <div className="character-info">
      <div className="row">
        <div className="col col-lg-1"></div>
        <div className="col col-lg-10">
          <ul className="character-info__dir">
            <li className="character-info__dir-item">
              <NavLink to={`/${GIT_PROJECT}`} className="character-info__dir-item-link">
                <FontAwesomeIcon
                  className="character-info__dir-icon"
                  icon={faChevronRight}
                />
                Trang chủ
              </NavLink>
            </li>
            <li className="character-info__dir-item">
              <NavLink
                to={`/${GIT_PROJECT}/characters`}
                className="character-info__dir-item-link"
              >
                <FontAwesomeIcon
                  className="character-info__dir-icon"
                  icon={faChevronRight}
                />
                Character
              </NavLink>
            </li>
            <li className="character-info__dir-item">
              <NavLink
                to={`/${GIT_PROJECT}/characters/${name}`}
                className="character-info__dir-item-link"
              >
                <FontAwesomeIcon
                  className="character-info__dir-icon"
                  icon={faChevronRight}
                />
                {name}
              </NavLink>
            </li>
          </ul>
        </div>
        <div className="col col-lg-1"></div>
      </div>
    </div>
  );
};

export default CharacterInfo;
